body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Montserrat";
  font-weight: normal;
  src: local("Montserrat"),
  url(/github-issue-explorer/static/media/montserrat-regular.3fb4d290.woff) format("woff"),
  url(/github-issue-explorer/static/media/montserrat-regular.b94d4bb6.woff2) format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-weight: bold;
  src: local("Montserrat-Bold"),
  url(/github-issue-explorer/static/media/montserrat-bold.b760b19f.woff) format("woff"),
  url(/github-issue-explorer/static/media/montserrat-bold.45af3727.woff2) format("woff2");
}

@font-face {
  font-family: "Montserrat-Light";
  src: local("Montserrat-Light"),
  url(/github-issue-explorer/static/media/montserrat-light.a820606b.woff) format("woff"),
  url(/github-issue-explorer/static/media/montserrat-light.d0d8af75.woff2) format("woff2");
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

::-webkit-scrollbar {
  display: none;
}
@-webkit-keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    display: none;
    pointer-events: none;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    display: none;
    pointer-events: none;
  }
}

@-webkit-keyframes fadein {
  from {
    background-color: black;
  }
  to {
    background-color: #E91E63;
  }
}

@keyframes fadein {
  from {
    background-color: black;
  }
  to {
    background-color: #E91E63;
  }
}

#search-page,
#search-page-hidden {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #E91E63; /* For Safari 3.1 to 6.0 */
  transition: height 1s;
}

#search-page {
  height: 100vh;
}

#search-page-hidden {
  height: 0vh;
  -webkit-animation: fadeout 0.4s;
          animation: fadeout 0.4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

#search-page-title {
  margin: 0 0 24px;
  font-size: 48px;
  font-weight: bold;
  color: white;
}

#form-inputs {
  display: flex;
  flex-direction: row;
  box-shadow: 0 2px 40px 0 rgba(0, 0, 0, 0.19);
}

#text-input {
  height: 80px;
  width: 48vw;
  padding: 0 0 0 16px;
  border: none;
  font-family: "Montserrat", sans-serif;
  font-size: 28px;
}

#button-container {
  background-color: #FFFFFF;
}

#search-button {
  display: block;
  height: 30px;
  width: 30px;
  padding: 25px;
  border: none;
  opacity: 0.3;
}

::-webkit-input-placeholder {
  font-family: "Montserrat-Light", sans-serif;
  font-size: 28px;
  color: #CCCCCC;
}

:-ms-input-placeholder {
  font-family: "Montserrat-Light", sans-serif;
  font-size: 28px;
  color: #CCCCCC;
}

:-moz-placeholder {
  font-family: "Montserrat-Light", sans-serif;
  font-size: 28px;
  color: #CCCCCC;
}

::-moz-placeholder {
  font-family: "Montserrat-Light", sans-serif;
  font-size: 28px;
  color: #CCCCCC;
}

@media only screen and (max-width: 850px) {
  ::-webkit-input-placeholder {
    font-size: 24px;
  }
  
  :-ms-input-placeholder {
    font-size: 24px;
  }
  
  :-moz-placeholder {
    font-size: 24px;
  }
  
  ::-moz-placeholder {
    font-size: 24px;
  }

  #text-input {
    padding: 0 0 0 8px;
    width: 56vw;
    font-size: 24px;
  }
}

@media only screen and (max-width: 500px) {
  #search-page-title {
    font-size: 28px;
  }

  #text-input {
    height: 64px;
    font-size: 14px;
  }

  #search-button {
    height: 20px;
    width: 20px;
    padding: 22px;
  }

  ::-webkit-input-placeholder {
    font-size: 14px;
  }
  
  :-ms-input-placeholder {
    font-size: 14px;
  }
  
  :-moz-placeholder {
    font-size: 14px;
  }
  
  ::-moz-placeholder {
    font-size: 14px;
  }

  #text-input {
    font-size: 14px;
  }
}
@-webkit-keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    display: none;
    pointer-events: none;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    display: none;
    pointer-events: none;
  }
}

#results-page,
#results-page-hidden {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; /* For Safari 3.1 to 6.0 */
  transition: height 1s;
}

#results-page {
  height: 100vh;
}

#results-page-hidden {
  -webkit-animation: fadeout 0.4s;
          animation: fadeout 0.4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  height: 0vh;
}

#results-header {
  z-index: 2;
  flex: 1 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  background-color: #E91E63;
  box-shadow: 16px 4px 16px 4px rgba(0, 0, 0, 0.19);
}

#results-header p {
  margin: 0 48px;
}

#header-title {
  font-size: 24px;
  font-weight: bold;
  color: #FFFFFF;
}

#url {
  font-family: "Montserrat-Light";
  font-size: 18px;
  color: #880E4F;
}

#standalone-message {
  flex: 8 1;
  font-size: 28px;
  color: #CFD8DC;
}

#results-content {
  flex: 8 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
}

#exit {
  z-index: 1;
  flex: 1 1;
  display: flex;
  width: 98%;
  justify-content: flex-end;
  align-items: flex-end; 
}

#exit-button {
  height: 24px;
  width: auto;
  margin-bottom: 8px;
  opacity: 0.6;
  cursor: pointer;
}

#filter-labels {
  flex: 0.8 1;
  display: flex;
  flex-direction: row;
  width: 90%;
}

.label-selected,
.label-unselected {
  font-size: 12px;
  font-family: "Montserrat-Light";
  margin: 0 10px;
  cursor: pointer;
}

.label-unselected {
  color: #607D8B;
}

.label-selected {
  color: #E91E63;
}

#issue-content {
  flex: 10 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  overflow-y: scroll;
}

/* Ensures space-between doesn't cause weird space in 2-item rows */
#issue-content:after {
  content: "";
  width: 366px;
}

@media only screen and (max-width: 500px) {
  #results-header {
    flex-direction: column;
    justify-content: space-around;
  }
}
.issue {
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 200px;
  margin: 0 8px 64px;
  box-shadow: 0 0 16px 1px rgba(0, 0, 0, 0.10);
}

.issue-header {
  position: relative;
  /* height: 100%; */
  background-color: #3F51B5;
}

.issue-status {
  position: absolute;
  top: 4px;
  right: 8px;
}

.status-symbol {
  height: 10px;
  width: 10px;
  opacity: 0.5;
}

.title-container {
  padding: 24px;
}

.issue-title {
  max-height: 2.8em;
  text-align: left;
  padding: 0;
  margin: 0;
  font-family: "Montserrat";
  font-size: 18px;
  line-height: 1.4em;
  color: #FFFFFF;
  overflow: hidden;
}

.issue-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 16px;
}

.issue-body {
  max-height: 2.8em;
  text-align: left;
  padding: 0;
  margin: 0;
  font-family: "Montserrat";
  font-size: 12px;
  line-height: 1.4em;
  color: #607D8B;
  overflow: hidden;
}

.no-body {
  text-align: left;
  padding: 0;
  margin: 0;
  font-family: "Montserrat";
  font-size: 12px;
  color: #CFD8DC;
  overflow: hidden;
}

.issue-labels {
  display: flex;
  justify-content: left;
  overflow: auto;
}

.bullet {
  height: 4px;
  width: 4px;
  padding-left: 6px;
}

.label-background {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
  border-radius: 4px;
  background-color: #E91E63;
}

.label-name {
  margin: 0;
  padding: 6px;
  font-size: 9px;
  font-family: "Montserrat-Light";
  color: #FFFFFF;
}
#spinner {
  flex: 10 1;
}

#spinner-icon {
  -webkit-animation: spin infinite 3s linear;
          animation: spin infinite 3s linear;
  height: 50px;
  width: 50px;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
